class Utils {};

Utils.Host = (page = '') => {
    var setting = 'dev'
    if (setting === 'production') {
        var protocol = location.protocol
        var hostname = location.hostname
        var port
        port = location.port
        return protocol + '//' + hostname + ':' + port + '/sertifikat' + page
    } else {   
        var HOST = 'http://localhost:3015'
        return HOST
    }
}

Utils.authHeader = () => {
    return {
        // headers: {
        //     "Access-Control-Allow-Origin": "*",
        //     "Access-Control-Allow-Methods": "GET, POST, PATCH, PUT, DELETE, OPTIONS",
        //     "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token, Accept, Content-Length, Accept-Encoding, X-CSRF-Token, Authorization",
        //     "Access-Control-Allow-Credentials": "true",
        //     authorization: "Bearer " + localStorage.getItem('jwt') || null,
        //     "Content-Type": "application/x-www-form-urlencoded",
        // }
        headers: {
			Authorization:
				"Bearer " + localStorage.getItem('jwt'),
				"Content-Type": "application/x-www-form-urlencoded",
			},
    };
}


export default Utils;
